	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Slashdot your comment spam","date":"02 Aug 2004","slug":"/2004-08-02-slashdot-your-comment-spam/","tags":"Y2004"},"html":"<p><a href=\"http://www.oblomovka.com/entries/2004/07/29#1091150520\">Danny O'Brien's Oblomovka: notes on protecting your open discussion forum</a> Comment spam? Slashdot has been there and done that. Some ideas here for blogging systems, and it solved a riddle we had at work (portscans that seem to come from high-volume websites are probably tests for open proxies, and no cause for alarm).</p>"},"next":{"fields":{"title":"BILDblog","date":"02 Aug 2004","slug":"/2004-08-02-bildblog/"}},"previous":{"fields":{"title":"UnNice Titles","date":"02 Aug 2004","slug":"/2004-08-02-unnice-titles/"}}},{"node":{"fields":{"title":"BILDblog","date":"02 Aug 2004","slug":"/2004-08-02-bildblog/","tags":"Y2004"},"html":"<p><a href=\"http://www.bildblog.de/\">BILDblog</a> is a blog on <a href=\"http://www.bild.t-online.de/\">Europe's largest newspaper</a>. Nice to see some <em>real</em> Medienkritiker blogging in Germany.</p>"},"next":{"fields":{"title":"Cat and Mouse","date":"01 Aug 2004","slug":"/2004-08-01-cat-and-mouse/"}},"previous":{"fields":{"title":"Slashdot your comment spam","date":"02 Aug 2004","slug":"/2004-08-02-slashdot-your-comment-spam/"}}},{"node":{"fields":{"title":"Cat and Mouse","date":"01 Aug 2004","slug":"/2004-08-01-cat-and-mouse/","tags":"Y2004"},"html":"<p>We  tried to tell my mother-in-law that she did not have to come feed our cat Nena while we were gone this weekend. I mean, we were only gone two days, and cats can survive for weeks without human attention. But she has a key to the house (my mother-in-law, not Nena), so we couldn't stop her. And she was shocked as Nena brought a mouse to the patio, and proceeded to eat the mouse, completely, on the spot, from head to tail.</p>\n<p>My mother-in-law loves cats, as she remembers the kitten she had as a young girl, but has never had a cat since her childhood, and thus doesn't really understand cats at all.</p>\n<p>I thought Nena was being quite considerate to her guest host. Usually she leaves the head for us to pick up in the morning.</p>"},"next":{"fields":{"title":"Beach Culture","date":"01 Aug 2004","slug":"/2004-08-01-beach-culture/"}},"previous":{"fields":{"title":"BILDblog","date":"02 Aug 2004","slug":"/2004-08-02-bildblog/"}}}],"pathPrefix":"","first":false,"last":false,"index":442,"pageCount":1101}}

